<template lang="html">
  <div class="container">
    <!-- <h1>Uno</h1> -->
    <img class='logo' src="./../assets/uno.png" alt="Uno">

    <div class="setUp" v-show="!start">
      Add players:<br/>
      <input type="text" v-model="newPlayer" v-on:keyup.enter="addPlayer()">
      <button type="button" name="button" @click="addPlayer()">Add</button>
      <div class="" v-for='(p, i) in players' :key="i">
        {{ p.name }}<br/>
      </div>
      <br>
      <br>
      <div>Point limit for 'survivor' mode: <input type="number" v-model="limitS"></div><br/>
      <div>Point limit for 'winner takes all' mode: <input type="number" v-model="limitW"></div>
      <button type="button" name="button" @click="startGame()">Start game</button>
    </div>

    <div class="gameOn" v-show="start">
      <div class="">
        <button type="button" name="button" @click="switchMode()">Switch mode</button>
        Mode: {{ mode }}
      </div>
      <h2>Score table</h2>
      <br>
      <div class="table-of-points">
        <div class="player-column" v-for="(p, i) in players" :key="i">
          <div class="outer">
            <div class="inner name">
              {{ p.name }}
            </div>
            <div class="inner" v-for="(score, j) in p.scores" :key="j">
              <span v-if="(mode === 'survivor' && (!gameOverA || (gameOverA && (gameOverARound >= j)))) || (mode === 'winnerTakesAll' && (!gameOverB || (gameOverB && (gameOverBRound >= j))))"
              :class="{ uno : (score === 'UNO') }">{{ score }}</span>
            </div>
            <div class="inner new-score" v-if="(mode ==='survivor' && !gameOverA) || (mode ==='winnerTakesAll' && !gameOverB)">
              <!-- <input type="checkbox" v-show="!roundOver" @click="uno(i)"> -->
              <button type="button" name="button" class="red" v-show="!roundOver" @click="uno(i)">Uno</button>
              <input type="text" v-model="p.lastScore" v-show="roundOver && (roundWinner != i)">
              <!-- <span v-show="roundOver && (winner == i)" @click="ununo()">UNO</span> -->
              <button type="button" name="button" class="green" v-show="roundOver && (roundWinner === i)" @click="ununo()">Uno</button>
            </div>
          </div>
          <div class="total" v-show="mode=='survivor'">
            {{ p.totalDownfall }}
          </div>
          <div class="total" v-show="mode=='winnerTakesAll'">
            {{ p.totalTakesAll }}
          </div>
        </div>
      </div>
      <br>
      <div class="center" v-show='roundOver'>
        <button type="button" name="button" @click="submitScores()">Submit</button>
      </div>
      <div class="center" v-show='!roundOver'>
        <button type="button" name="button" @click="resetScore()">Reset score</button>
        <button type="button" name="button" @click="resetGame()">Reset game</button>
        <!-- <button type="button" name="button" @click="saveToDb()">Not available yet</button> -->
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'uno',
  data () {
    return {
      newPlayer: '',
      players: [],
      limitS: 200,
      limitW: 200,
      start: false,
      mode: 'survivor',
      roundOver: false,
      roundWinner: -1,
      gameOverA: false,
      gameOverARound: -1,
      gameOverB: false,
      gameOverBRound: -1
    }
  },
  methods: {
    addPlayer () {
      if (this.newPlayer !== '') {
        this.players.push({ name: this.newPlayer, scores: [], lastScore: 0, totalDownfall: null, totalTakesAll: 0 })
        this.newPlayer = ''
      }
    },
    startGame () {
      if (this.limitS !== '' && this.limitW !== '' && this.players.length >= 2) {
        this.start = true
        this.players.forEach(p => p.totalDownfall = this.limitS)
        document.documentElement.style.setProperty("--numPlayers", this.players.length)
        localStorage.setItem('limitS', this.limitS)
        localStorage.setItem('limitW', this.limitW)
      } else {
        console.log('start not')
      }
    },
    switchMode () {
      if (this.mode === 'survivor') {
        this.mode = 'winnerTakesAll'
      } else {
        this.mode = 'survivor'
      }
    },
    uno (index) {
      this.roundWinner = index
      this.roundOver = true
      this.players[index].lastScore = 'UNO'
    },
    ununo () {
      document.querySelectorAll('.new-score input[type=checkbox]').forEach(el => {
        el.checked = false
      })
      this.roundOver = false
      this.roundWinner = -1
    },
    submitScores () {
      let winnerScore = 0
      let goa = false
      this.players.forEach(p => {
        p.scores.push(p.lastScore)
        if (p.lastScore != 'UNO') {
          // first update scores for downfall mode
          if (!this.gameOverA) {
            p.totalDownfall -= p.lastScore

            if (p.totalDownfall <= 0) {
              goa = true
              this.gameOverARound = p.scores.length-1
            }
          }
          // calculate score for winner takes all mode
          winnerScore += parseInt(p.lastScore)
        }
      });
      if (goa) {
        this.gameOverA = true
      }

      this.players.forEach(p => {
        if (p.lastScore === 'UNO') {
          if (!this.gameOverB) {
            p.totalTakesAll += winnerScore
            if (p.totalTakesAll >= this.limitW) {
              this.gameOverB = true
              this.gameOverBRound = p.scores.length-1;
            }
          }

        }
        p.lastScore = 0
      });
      // save
      localStorage.setItem('savedUnoData', JSON.stringify(this.players))
      this.ununo()
    },
    loadSavedGame (data) {
      this.players = JSON.parse(data)
      document.documentElement.style.setProperty("--numPlayers", this.players.length)
      this.start = true
    },
    resetScore () {
      const r = confirm('Reset scores? u sure?')
      if (r) {
        this.players.forEach(p => {
          p.scores = []
          p.lastScore = 0
          p.totalDownfall = this.limitS
          p.totalTakesAll = 0
        })
      } else {
        // do nothing
      }

    },
    resetGame () {
      const r = confirm('Reset game? u sure?')
      if (r) {
        this.players = [],
        this.limitS = 200,
        this.limitW = 200,
        this.start = false,
        this.mode = 'survivor',
        this.roundOver = false,
        this.roundWinner = -1,
        this.gameOverA = false,
        this.gameOverARound = -1,
        this.gameOverB = false,
        this.gameOverBRound = -1
      } else {
        // do nothing
      }

    },
    saveToDb () {

    }
  },
  mounted () {
    if (localStorage.getItem('savedUnoData')) {
      this.loadSavedGame(localStorage.getItem('savedUnoData'))
    }
    localStorage.getItem('limitS') ? this.limitS = JSON.parse(localStorage.getItem('limitS')) : undefined
    localStorage.getItem('limitW') ? this.limitW = JSON.parse(localStorage.getItem('limitW')) : undefined
  }
}
</script>

<style lang="scss" scoped>
:root {
  --numPlayers: 4;
  // --colNum: 5;
  // --gridHeight: 120px;
  // --wrapperH: 40px; // for demonstrating the use of CSS Variables
}
.container {
  text-align: center;

  .logo {
    max-width: 100px;
  }
  .setUp {
    max-width: 500px;
    margin: auto;
    padding: 20px;
  }
  .gameOn {
    text-align: center;
    .table-of-points {
      max-width: 500px;
      display: grid;
      grid-template-columns: repeat(var(--numPlayers), 1fr);
      margin: auto;
      .player-column {
        display: inline-block;
        border: 1px solid lightgray;
        border-top: none;
        border-bottom: none;
        /*width: 30%*/
        .inner {
          &.name {
            text-decoration: underline;
          }
          .uno {
            color: green;
          }
        }
        .new-score {
          height: 50px;
          line-height: 50px;
          input {
            text-align: center;
          }
          .red {
            background-color: red;
          }
          .green {
            background-color: green;
          }
        }
        .total {
          border-top: 1px solid gray;
        }
      }
    }
  }
}

</style>



